

.ask-question{
    min-height: 100vh;
    background-color: #e5e6e8;
}

.ask-ques-container{
    margin: auto;
    max-width: 1200px;

}
.ask-ques-container h1{
    padding: 80px 0px 20px 0px;

}


.ask-ques-container form .ask-form-container{
    padding: 20px;
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 3px 3px 10px rgb(0 0 0 / 5%),
    -3px -3px 10px rgb(0 0 0 / 5%);
}

.ask-ques-container label h4{
    margin-bottom: 0%;

}

.ask-ques-container label p{
    margin: 0%;
    font-size: 13px;
    padding: 3px 0px;
}

.ask-form-container label input{
    padding: 10px;
    border: solid 1px #dea6a63e;
    font-family: 'Roboto','sans-serif';
    width: 96%;
    resize: vertical;
}

.ask-form-container textarea{
    padding: 10px;
    border: solid 1px #dea6a63e;
    font-family: 'Roboto','sans-serif';
    width: 96%;
    resize: vertical;
}


.review-btn{
    margin: 50px 0px;
    padding: 10px;
    background-color: #009dff;
    border: solid 1px #009dff;
    cursor: pointer;
    color: #fff;
    border-radius: 4px;
    transition: 0.2s;
}

.review-btn:hover{
    background-color: #0086d8;
}